*{
    padding:0;
    margin:0;
}

body{
    background-color:rgb(240, 240, 245);
    min-height:100vh;
}
#board{
    border:2px solid black;
}

#root{
    display:flex;
    flex-direction:column;
    align-items:center;
    gap:10px;
}

#gameplay{
    display:flex;
    gap:10px;
}

.buttons{
    display:flex;
    align-items:right;
    gap:10px;
    justify-content: flex-end;
}

#stats{
    display:flex;
    gap:10px;
}

.buttons label{
    display:flex;
    gap:5px;
}

#victoryMsg{
    font-size:30px;
    color:green;
}

